  
html {
  min-height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image: url(https://veterinariotucan.com/wp-content/uploads/2020/08/pet-1.svg);
  background-size:cover;
  background-attachment:fixed;

  min-height: 100%;
}
h1, h2  {
  color: #000;
  padding: 3rem 0;
  text-align: center;

  text-transform: uppercase;

  font-family: 'Staatliches', cursive;
  letter-spacing: 1px;
}
.lista-citas .media {
  border-bottom: 1px solid #e1e1e1;
  padding-bottom: 3rem;
}

.lista-citas .media:last-of-type{
  border-bottom: 0;
}
.lista-citas .media-body p {
  margin:0;
}
.lista-citas .media-body button {
  margin-top: 1rem!important;
}
.lista-citas .media-body span {
  font-weight: bold;
}

label {
  color: #000;
}

input[type="date"],
input[type="time"] {
  height: 38px;
  padding: 6px 10px;
  background-color: #fff;
  border: 1px solid #D1D1D1;
  border-radius: 4px;
  box-shadow: none;
  box-sizing: border-box;
}

.cita {
  background-color: rgba(211, 84, 0, 0.2);
  padding: 2rem;
  border-bottom: 1px solid #e1e1e1;
  border-radius: 5px;
  margin-bottom: 5px;
}

.cita p {
  font-weight: bold;
  margin-bottom: .8rem;
  
}
.cita p span {
  font-weight: normal;
  
}
.cita:first-of-type {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}
.cita:last-of-type {
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}
.button.eliminar,
.button.eliminar:hover {
  background-color: #c10059;
  color: white;
  margin-top: 2rem;
}

.alerta-error {
  background-color: white;
  color: red;
  padding: 1rem;
  font-size: 2rem;
  text-transform: uppercase;
  text-align: center;
  font-family:  'Staatliches', cursive;
} 

.footer{
  background-color: rgba(211, 84, 0, 0.2);
  color: #000;
  font-size: 19px;
  height: 50px;
  position: relative;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left : 10px;
  padding-right : 10px;
}